/**
* Convert PX to EM
*
* em-convert is a function of the gulp postcss-em package
*
**/
/**
* Example
**/
/**
* Media queries
**/
.header {
  background-color: var(--clr-white);
}
.header .vertical-line {
  height: 24px;
  margin: 0 16px;
}
.header__address-bar {
  padding: 22px 0;
}
@media only screen and (max-width: 767px) {
  .header__address-bar {
    padding: 12px 0;
    display: none;
  }
}
.header__address-bar .location,
.header__address-bar .settings {
  display: flex;
  align-items: center;
}
.header__address-bar .location {
  gap: 0 12px;
}
.header__address-bar .location:hover .location__text {
  color: var(--clr-red);
}
.header__address-bar .location:hover .location__pin {
  transform: scale(1.1);
}
.header__address-bar .location:hover .location__pin svg path {
  fill: var(--clr-red);
}
@media only screen and (max-width: 767px) {
  .header__address-bar .location {
    justify-content: center;
    margin-bottom: 5px;
  }
}
.header__address-bar .location__pin {
  transition: all 0.6s ease-in-out;
}
.header__address-bar .location__pin svg path {
  transition: all 0.6s ease-in-out;
}
.header__address-bar .location__text {
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-5);
  color: var(--clr-smoke-2);
  line-height: 20px;
  padding: 0;
  transition: all 0.6s ease-in-out;
}
.header__address-bar .settings {
  justify-content: flex-end;
}
@media only screen and (max-width: 767px) {
  .header__address-bar .settings {
    justify-content: center;
  }
}
.header__address-bar .settings select {
  border: none;
  background-color: transparent;
}
.header__address-bar .settings__currency, .header__address-bar .settings__lang, .header__address-bar .settings__order-track {
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-5);
  color: var(--clr-smoke-2);
  line-height: 20px;
}
.header__address-bar .settings__order-track:hover a {
  color: car(--clr-red);
}
.header__address-bar .settings__order-track a {
  color: var(--clr-smoke-2);
}
.header__logo-bar {
  padding: 22px 0;
}
.header__logo-bar .row {
  align-items: center;
}
.header__logo-bar .location,
.header__logo-bar .settings {
  display: flex;
  align-items: center;
}
.header__logo-bar .logo {
  margin: 0 auto;
}
@media only screen and (max-width: 767px) {
  .header__logo-bar .logo {
    margin: 0 auto 0 0;
  }
}
.header__logo-bar .logo svg,
.header__logo-bar .logo img {
  width: 100%;
  height: auto;
}
.header__logo-bar .icon-group {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.header__logo-bar .icon-group .user {
  display: flex;
  align-items: center;
  gap: 0 16px;
}
.header__logo-bar .icon-group .user__text {
  font-family: var(--font-family-2-1);
  font-size: var(--theme-font-p-mob-1);
  color: var(--clr-smoke-2);
  line-height: 24px;
  text-transform: uppercase;
  opacity: 0.75;
  transition: all 0.5s ease-in-out;
}
.header__logo-bar .icon-group .user svg path {
  transition: all 0.5s ease-in-out;
}
.header__logo-bar .icon-group .user:hover .user__text {
  color: var(--clr-red);
  opacity: 1;
}
.header__logo-bar .icon-group .user:hover svg path {
  fill: var(--clr-red);
}
.header__logo-bar .icon-group .cart__icon svg {
  transition: all 0.3s ease-in-out;
}
.header__logo-bar .icon-group .cart:hover .cart__icon svg {
  transform: scale(1.1);
}
.header__logo-bar .search:hover svg {
  transform: scale(1.1);
}
.header__logo-bar .search form {
  display: flex;
  gap: 12px;
}
.header__logo-bar .search input {
  width: 100%;
  max-width: 236px;
  border: none;
}
.header__logo-bar .search button {
  background-color: transparent;
  border: none;
}
.header__logo-bar .search svg {
  transition: all 0.5s ease-in-out;
}
.header__logo-bar .hamburger {
  display: flex;
  flex-direction: column;
  gap: 5px;
  max-width: 25px;
  margin: 0 0 0 auto;
}
.header__logo-bar .hamburger span.line {
  width: 100%;
  height: 3px;
  background-color: var(--clr-red);
}
.header--sticky {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 9999;
}